/**
 * Global */
@font-face {
  font-family: 'Keyrune';
  src: url('../fonts/keyrune.eot?v=3.17.0');
  src: url('../fonts/keyrune.eot?#iefix&v=3.17.0') format('embedded-opentype'), url('../fonts/keyrune.woff2?v=3.17.0') format('woff2'), url('../fonts/keyrune.woff?v=3.17.0') format('woff'), url('../fonts/keyrune.ttf?v=3.17.0') format('truetype'), url('../fonts/keyrune.svg?v=3.17.0#keyrune') format('svg');
  font-weight: normal;
  font-style: normal;
}
.ss {
  display: inline-block;
  font: normal normal normal 14px/1 Keyrune;
  font-size: inherit;
  line-height: 1em;
  text-rendering: auto;
  transform: translate(0, 0);
  speak: none;
  text-transform: none;
  vertical-align: middle;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.ss:before {
  content: "\e684";
}
/**
 * Larger sizes */
.ss-2x {
  font-size: 2em;
}
.ss-3x {
  font-size: 3em;
}
.ss-4x {
  font-size: 4em;
}
.ss-5x {
  font-size: 5em;
}
.ss-6x {
  font-size: 6em;
}
/**
 * Handling for set symbols that use an inner and outer layer, like M21
 *
 * The inner layer is what gets colorized and is the traditional "set symbol"
 * The outer layer takes over as the border, and is white for common cards
 * but black for uncommon, rare, and mythic.
 */
.ss-duo {
  color: #fff;
  /**
     * white sections are ::after
     * color sections are ::before */
}
.ss-duo::after {
  color: #000;
  content: '';
  display: block;
  font-size: 1em;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
.ss-duo.ss-40k::before {
  content: "\e999";
  width: 100%;
}
.ss-duo.ss-40k::after {
  content: "\e99b";
  color: #fff !important;
}
.ss-duo.ss-xdnd::after,
.ss-duo.ss-afr::after {
  content: "\e973";
}
.ss-duo.ss-afc::after {
  content: "\e982";
}
.ss-duo.ss-c21::after {
  content: "\e97f";
}
.ss-duo.ss-clb::after {
  content: "\e992";
}
.ss-duo.ss-cc2::after {
  content: "\e988";
}
