  /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(left, #876a3b 0%, #dfbd6b 50%, #876a3b 100%);
  background: linear-gradient(90deg, #876a3b 0%, #dfbd6b 50%, #876a3b 100%);
}
.ss-mythic {
